  cursor: pointer;
}
.post-card{
  background-color: white;
  padding: 10px;
  margin-top: 40px;
  width: 40%;
  border-radius: 5px;
  box-shadow: 42px 41px 37px -55px rgba(0,0,0,0.75);
  cursor: pointer;
  transition: -webkit-transform .3s ease;
  transition: transform .3s ease;
  transition: transform .3s ease, -webkit-transform .3s ease;
}
.post-card .blog-image{
 height: -webkit-fit-content;
 height: -moz-fit-content;
 height: fit-content;
}
.post-card .blog-image img{
  width:  100%;
  height: 200px;
  object-fit: cover;
}
.post-card .blog-details .date{
  font-size: 12px;
  font-weight: 500;
  margin: 10px 0px;
}
.post-card .blog-details .title{
  color: black;
  font-weight: 700;
  margin-bottom: 10px;
}
.post-card .blog-details .brief{
  color: rgb(67, 66, 66);
}
.post-card .blog-details .read-full{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-top: 10px;
}
.post-card .blog-details .read-full a{
  color: crimson;
  text-decoration: none;
}
.post-card .read-full .fa-long-arrow-right{
  margin-left: 10px;
  vertical-align: middle;
}
.post-card .fa-comment, .post-card .fa-thumbs-up{
  margin-left: 5px;
  cursor: pointer;
}
@media (max-width: 1199.98px) { 
