.blog-content h1 {
  color: #111827;
  font-weight: 800;
  font-size: 2.25em;
  margin-top: 0;
  margin-bottom: .8888889em;
  line-height: 1.1111111;
}
.blog-content h2 {
  color: #111827;
  font-weight: 700;
  font-size: 1.5em;
  margin-top: 2em;
  margin-bottom: 1em;
  line-height: 1.3333333;
}
.blog-content h3 {
  color: #111827;
  font-weight: 600;
  font-size: 1.25em;
  margin-bottom: .6em;
  line-height: 1.6;
}

.blog-content blockquote {
  font-weight: 500;
  font-style: italic;
  color: #111827;
  border-left: .25rem solid #e5e7eb;
  quotes: "\201C""\201D""\2018""\2019";
  margin-top: 1.6em;
  margin-bottom: 1.6em;
  padding-left: 1em;
}
.blog-content p {
  margin-top: 1.25em;
  margin-bottom: 1.25em;
  line-height: 30px;
}


.blog-content pre {
  color: #e5e7eb;
  background-color: #1f2937;
  overflow-x: auto;
  font-size: .875em;
  line-height: 1.7142857;
  margin-top: 1.7142857em;
  margin-bottom: 1.7142857em;
  border-radius: .375rem;
  padding-top: .8571429em;
  padding-right: 1.1428571em;
  padding-bottom: .8571429em;
  padding-left: 1.1428571em;
}

.blog-content pre code {
  background-color: transparent;
  border-radius: 0;
  font-weight: 500;
  padding: 0;
  font-size: 100%;
}


.blog-content a {
  color: #111;
  text-decoration: underline;
  font-weight: 500;
}


.blog-content .embed-wrapper {
  max-width: 100%;
  margin-top: 1.25em;
  margin-bottom: 1.25em;
}

.blog-content ol, .blog-content ul {
 
  margin: 18px;
  padding: 0;
}
.blog-content ul{
   list-style: none;
    margin: 0;
    padding: 0;
}

.blog-content ol {
  margin-top: 1.25em;
  margin-bottom: 1.25em;
}



.blog-content li {
  margin-top: .5em;
  margin-bottom: .5em;
}

.blog-content ul {
  margin-top: 1.25em;
  margin-bottom: 1.25em;
}

.blog-content ul>li {
  position: relative;
  padding-left: 1.75em;
}

.blog-content ul>li::before {
  content: "";
  position: absolute;
  background-color: #d1d5db;
  border-radius: 50%;
  width: .375em;
  height: .375em;
  top: calc(0.875em - 0.1875em);
  left: .25em;
}

.blog-content img, video {
  margin-top: 2em;
  margin-bottom: 2em;
  display: block;
  max-width: 100%;
  height: auto;
}
iframe{
  width: 100%;
